import {Action} from '@ngrx/store';
import {JobsModel} from '../jobs.model';

export const TRY_FETCH_JOBS = 'TRY_FETCH_JOBS';
export const TRY_ADD_JOBS = 'TRY_ADD_JOBS';
export const TRY_EDIT_JOB = 'TRY_EDIT_JOB';
export const TRY_DELETE_JOB = 'TRY_DELETE_JOB';
export const SET_JOBS = 'SET_JOBS';

export class SetJobs implements Action {
  readonly type = SET_JOBS;

  constructor(public payload: JobsModel[]) {
  }
}

export class TryDeleteJob implements Action {
  readonly type = TRY_DELETE_JOB;

  constructor(public payload: string) {
  }
}

export class TryEditJob implements Action {
  readonly type = TRY_EDIT_JOB;

  constructor(public payload: JobsModel) {
  }
}

export class TryAddJobs implements Action {
  readonly type = TRY_ADD_JOBS;

  constructor(public payload: JobsModel) {
  }
}

export class TryFetchJobs implements Action {
  readonly type = TRY_FETCH_JOBS;
}


export type JobsActions = TryFetchJobs
  | TryEditJob | TryDeleteJob | SetJobs
  | TryAddJobs;
