import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {JobsComponent} from './jobs.component';
import {DashboardComponent} from './dashboard/dashboard.component';
import {PostjobComponent} from './postjob/postjob.component';
import {JoblistComponent} from './joblist/joblist.component';
import {ApplicantlistComponent} from './applicantlist/applicantlist.component';

const routes: Routes = [
    {
        path: '', component: JobsComponent, children: [
            {path: '', redirectTo: 'dashboard'},
            {path: 'dashboard', component: DashboardComponent},
            {path: 'postJob', component: PostjobComponent},
            {path: 'editJob/:id', component: PostjobComponent},
            {path: 'list', component: JoblistComponent},
            {path: 'applicant', component: ApplicantlistComponent}
        ]
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class JobsRoutingModule {
}
