import {Component, OnInit} from '@angular/core';
import {Store} from '@ngrx/store';
import {JobState} from '../store/jobs.reducer';
import {JobsModel} from '../jobs.model';
import * as JobsActions from '../store/jobs.actions';
import {ActivatedRoute, Router} from '@angular/router';

@Component({
  selector: 'app-joblist',
  templateUrl: './joblist.component.html',
  styleUrls: ['./joblist.component.css']
})
export class JoblistComponent implements OnInit {

  jobs: JobsModel[];

  constructor(private store: Store<JobState>, private router: Router, private route: ActivatedRoute) {
  }

  ngOnInit() {
    this.store.select('jobs').subscribe(
      value => {
        console.log(value);
        if (value.jobs !== undefined) {
          this.jobs = value.jobs;
        }
      }
    );
  }

  editJob(index: number) {
    this.router.navigate(['../editJob', index], {relativeTo: this.route});
  }

  deleteJob(index: number) {
    this.store.dispatch(new JobsActions.TryDeleteJob(this.jobs[index]._id));
  }

}
