import {AppState} from '../../../../store/app.reducer';
import {JobsModel} from '../jobs.model';
import {JobsActions} from './jobs.actions';

export interface JobState extends AppState {
  jobs: State;
}

export interface State {
  jobs: JobsModel[];
}

const initialState: State = {
  jobs: []
};

export function JobsReducer(state = initialState, action: JobsActions) {
  switch (action.type) {
    case 'SET_JOBS': {
      console.log(action.payload);
      const finalJob = [];
      for (const job of action.payload) {
        finalJob.push(job);
      }
      return {
        ...state,
        jobs: finalJob
      };
    }
    default: {
      return {
        ...state
      };
    }
  }
}
