import {Injectable} from '@angular/core';
import {Actions, Effect, ofType} from '@ngrx/effects';
import {HttpClient, HttpResponse} from '@angular/common/http';
import * as JobsActions from './jobs.actions';
import {map, switchMap} from 'rxjs/operators';
import {JobsModel} from '../jobs.model';

import * as endPoints from '../../../../shared/serverEndpoints';

@Injectable()
export class JobsEffects {
  constructor(public action: Actions, public http: HttpClient) {
  }

  @Effect()
  addJob = this.action.pipe(
    ofType(JobsActions.TRY_ADD_JOBS),
    map((data: JobsActions.TryAddJobs) => {
      return data.payload;
    }),
    switchMap((data: JobsModel) => {
      return this.http.post(endPoints.newJob, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: JobsActions.SET_JOBS,
            payload: response.body
          }
        ];
      } else {
        alert('Some error occured !');
        return [];
      }
    })
  );

  @Effect()
  fetchAllJobs = this.action.pipe(
    ofType(JobsActions.TRY_FETCH_JOBS),
    switchMap(() => {
      return this.http.get(endPoints.fetchJobByUser, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: JobsActions.SET_JOBS,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

  @Effect()
  deleteJob = this.action.pipe(
    ofType(JobsActions.TRY_DELETE_JOB),
    map((data: JobsActions.TryDeleteJob) => {
      return data.payload;
    }),
    switchMap((data: string) => {
      return this.http.post(endPoints.editJob, {_id: data, delFlg: 'Y'}, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: JobsActions.SET_JOBS,
            payload: response.body
          }
        ];
      } else {
        return [];
      }
    })
  );

  @Effect()
  editJob = this.action.pipe(
    ofType(JobsActions.TRY_EDIT_JOB),
    map((data: JobsActions.TryEditJob) => {
      return data.payload;
    }),
    switchMap((data: JobsModel) => {
      return this.http.post(endPoints.editJob, data, {observe: 'response'});
    }),
    switchMap((response: HttpResponse<any>) => {
      if (response.status === 200) {
        return [
          {
            type: JobsActions.SET_JOBS,
            payload: response.body
          }
        ];
      } else {
        alert('Some error occured !');
        return [];
      }
    })
  );
}
